@font-face {
  font-family: Poppins;
  src: url(./Poppins-Regular.otf);
}
* {
	padding: 0;
	margin: 0; 
	list-style-type: none; 
	font-size: 20px;
	font-family: Poppins , sans-serif;
}
/*HOME*/
.home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.open-favourites {
	position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
}
.open-favourites svg {
    background: #a6e0e180;
    color: #323232;
    padding: 3px;
    height: 40px;
    width: 40px;
}
form.search-container {
    position: fixed;
    width: 100%;
    background: radial-gradient(#e9e9e900, #00000020);
    height: 100%;
    margin: 0px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.4s; 
    background-size: cover;
}

form.search-container input.search-input {
    height: 40px;
    width: 65%;
    border-radius: 5px;
    font-size: 20px;
    text-indent: 20px;
    margin-right: 10px;
    outline: none;
    background: #000000ad;
    border: none;
    color: #fff;
}
form.search-container input::placeholder {
	color: #fff;
}
form.search-container div.back {
	display: none;
}
form.search-container .search {
    width: 20%;
    height: 40px;
    border: none;
    border-radius: 5px;
    background: #000000ad;
    color: #fff;
    font-size: 17px;
    cursor: pointer;
}
form.search-container.up {
	height: 55px;
    z-index: 200;
    background: #000;
}
form.search-container.up div.back {
    display: none;
    background: #000000ad;
    height: fit-content;
    width: fit-content;
    border: none;
    outline: none;
    padding: 1px 0;
}
form.search-container.up div.back svg {
    transform: rotate(180deg);
    height: 35px;
    width: 40px;
    color: white;
}
.random-title {
	-webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    display: -webkit-box;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    width: fit-content;
    color: #000000;
    margin: 0px auto;
    position: absolute;
    top: 25%;
    font-size: 25px; 
    z-index: 1; 
    padding: 6px 15px;
    background: #ffffff52;
    border-radius: 10px;
}
.random-details {
	-webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    display: -webkit-box;
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 85%;
    color: #000000;
    margin: 0px auto;
    position: absolute;
    bottom: 15%; 
    padding: 6px 15px;
    background: #ffffff52;
    border-radius: 10px;
}
/*SEARCH*/
.search-list {
	position: fixed;
    z-index: 100;
    background: #141419;
    width: 100%;
    height: 93%;
    padding-top: 60px;
    overflow: scroll;
    transition: 0.4s; 
    color: white;
}
.search-list ol {
    display: grid;
}
.search-list ol li {
    width: 85vw;
    background: #fff;
    border-radius: 10px;
    margin: 10px auto;
    overflow: hidden;
    box-shadow: 0 0 6px 1px #ffffff70;

}
.search-list ol li img {
    width: 100%;
    object-fit: cover;
    height: 180px;
}
.search-list ol li footer {
    display: flex;
    align-items: center;  
    justify-content: space-between;
    padding: 5px 7px;
} 
.search-list ol li footer span {
    color: #1b1b1b;
    width: 90%;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.search-list ol li footer button {
    width: 40px;
    height: 40px;
    overflow: hidden;
    background: none;
    border: none;
}
.search-list ol li footer button svg {
    height: 40px;
    width: 40px;
}
.swap-random-meal {
    z-index: 4;
    bottom: 4%;
    position: fixed;
    right: 5%;
    cursor: pointer;        
}
.swap-random-meal svg {
    background: #a6e0e1b0;
    color: #323232;
    padding: 5px;
    border-radius: 50%;
    border: 4px solid #000000c4;
    height: 40px;
    width: 40px;

}

@media only screen and (min-width: 600px) {
    form.search-container input.search-input {
        width: 400px
    }
   form.search-container .search {
    width: 100px;
   }
}
@media only screen and (min-width:  1200px) {
    .random-details {
        width: 700px;
        margin-left: 15%;
    }
    .favourites-block{
        width: unset;
    }
    .favourites-content{
        width: 260px;
    }
}
/*MODAL*/
.modal-container {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100%;
	position: fixed;
	background: #000000de;
	top: 0;
	z-index: 500;
	left: 0;
}
.modal-content {
    width: 85vw;
    height: 85vh;
    max-width: 800px;
    background: #fff;
    border-radius: 5px;
    overflow: scroll;
    position: relative;
}
.modal-content .fav { 
    position: fixed;
    transform: translate(70vw, 0%);
    padding: 5px;
    transition: .3s;
    color: white;
    overflow: hidden;
    z-index: 10;
}
.modal-content .modal-image {
    width: 100%;
    height: 300px;
    object-fit: cover;
}
.modal-details {
	 padding: 10px 15px;
}
.modal-details h5 {
	font-size: 17px;
    padding: 17px 0;
}
.modal-details h6 {
	font-size: 15px;
}
.modal-content footer {
    display: flex;
    flex-direction: column;
    width: fit-content;
    padding: 10px 15px;
}
.modal-content footer a {
    color: #3c3cb4;
    padding-bottom: 20px;
    font-size: 18px;
}
.modal-content footer button {
    margin-bottom: 20px;
    width: fit-content;
    padding: 4px 15px;
    border-radius: 5px;
    border: none;
    outline: none;
    background: #720d25;
    color: white;
    font-size: 15px;
}

/*FAVOURITES*/
.favourites-block {
	display: flex;
	flex-direction: column;
	background: #1b1b1b50;
	color: white;
	position: fixed;
	left: 0;
	transition: .5s;
	top: 0;
	width: 100%;
	height: 100%;
	z-index : 5;
}
.favourites-block.hide {
	left: -100%;
}
.favourites-content {
	background: #1b1b1b;
	width: 70%;
	height: 100%;
	padding: 10px 20px;
	position: relative;
    max-width: 300px;
}
.close-favourites-modal {
    position: absolute;
    top: 0px;
    right: -30px;
    background: #1b1b1b;
    padding: 3px 5px;
}
.close-favourites-modal svg {
    transform: rotate(180deg);
    height: 40px;
    width: 40px;
}
.fav-lists {
    overflow: scroll;
    height: 92vh;
}
.fav-lists li {
    background: #fff;
    border-radius: 10px;
    max-width: 300px;
    margin: 16px 0;
    overflow: hidden;
    box-shadow: 0 0 6px 1px #ffffff70;
}
.fav-lists li img {
	width: 100%;
	height: 80px;
	object-fit: cover;
}
.fav-lists li footer {
    display: flex;
    align-items: center;  
    justify-content: space-between;
    padding: 0 7px;
} 
.fav-lists li footer span {
    color: #1b1b1b;
    width: 80%;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.fav-lists li footer button {
    width: 30px;
    height: 30px;
    overflow: hidden;
    background: none;
    border: none;
}
.fav-lists li footer button svg {
    height: 30px;
    width: 30px;
}
@media only screen and (min-width:  1024px) {
    .random-title , form.search-container input.search-input {
        margin-left: 28%; 
    }
    .random-details {
    width: 650px;
    margin-left: 28%;
    }
    .favourites-block{
        width: unset;
    }
    .favourites-content{
        width: 260px;
    }
    .close-favourites-modal{
        display: none;
    } 
    .modal-content .modal-image{
        position: relative;
        top: -30px;
    }
    .modal-details {
        margin-top: -30px;
    }
    .modal-content .fav {
        position: sticky;
        top: 2px;
    }
}
@media only screen and (min-width:  1200px) {
    .random-title , form.search-container input.search-input {
        margin-left: 15%; 
    }
    .random-details {
        width: 700px;
        margin-left: 15%;
    }
    .favourites-block{
        width: unset;
    }
    .favourites-content{
        width: 260px;
    }
    .close-favourites-modal{
        display: none;
    } 
    .modal-content .modal-image{
        position: relative;
        top: -30px;
    }
    .modal-details {
        margin-top: -30px;
    }
    .modal-content .fav {
        position: sticky;
        top: 2px;
    }
}
svg {
    height: 50px;
    width: 50px;
    display: block;
    transition: .3s;
}
.loading-meals {
    position: fixed;
    height: 100%;
    width: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
}
.loading-meals svg {
    height: 80%;
    width: 80%;
    animation: loadingMeal 1.8s infinite;
}
    
@keyframes loadingMeal {
    0% {
        height: 50%;
        opacity: .4;
        width: 50%;
    }
    50% {
        height: 80%;
        width: 80%;
        opacity: 1;
    }
    100% {
        height: 50%;
        opacity: .4;
        width: 50%;
    }
}
.no-result {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.no-result span {
    width: 80%;
    text-align: center;
}
.no-result img {
    width: 80%;
}
.offline-message {
    width: 100%;
    position: fixed;
    z-index: 600;
    margin: 0px auto;
    bottom: 5vh;
    align-items: center;
    display: flex;
    justify-content: center;
}
.offline-message div {
    display: flex;
    background: #0000009c;
    padding: 5px 20px;
    border-radius: 5px;
    color: white;
    align-items: center;
}
.offline-message span {
    font-size: 13px
}
.offline-message svg {
    height: 23px;
    width: 23px;
    margin-left: 10px;
}